@import url(https://use.fontawesome.com/releases/v5.13.0/css/all.css);
body {
  margin: 0;
  background-image: url("https://i.pinimg.com/originals/77/0b/c5/770bc50cee014a808e818037164ac7fe.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgba(2, 1, 6, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}


.form {
  width: 325px;
  margin: 40px auto;
}

.input_wrapper {
  position: relative;
}

.input_wrapper:before {
  bottom: 0;
  margin: auto 0;
  font-family: "Font Awesome\ 5 Free";
  content: "\f002";
  font-weight: 600;
  color: rgb(75, 173, 233);
  right: 14px;
  top: 12px;
  padding: 0;
  position: absolute;
  pointer-events: none;
}

input[type=text] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 300px;
  height: 36px;
  border-radius: 20px;
  border-color: white;
  outline: none;
  padding-left: 20px;
}

@media (min-width: 768px) {
  .form {
    margin: 100px auto;
  }
}
.Weather_main__30e43 {
  width: 350px;
  margin: 0 auto;
}

.Weather_info__23i87 {
  background-color: rgba(21, 29, 66, 0.8);
  width: 350px;
  height: 500px;
  border-radius: 20px;
  color: aliceblue;
  margin: 0;
}

.Weather_location__Wic-L {
  margin: 20px auto;
  width: 250px;
  text-align: center;
  padding-top: 10px;
}

.Weather_location__Wic-L h1, h2 {
  margin: 0;
}

.Weather_location__Wic-L p {
  font-size: 12px;
  margin: -20px 0 0 0;
}

.Weather_weather_wrapper__xFaSS {
  width: 250px;
  margin: 70px auto;
}

.Weather_weather_wrapper__xFaSS p {
  margin: 5px 0;
  font-size: 18px;
}

@media (min-width: 768px) {
  .Weather_main__30e43 {
    width: 700px;
  }

  .Weather_info__23i87 {
    width: 700px;
    height: 300px;
    border-radius: 50px;
    display: flex;
    justify-content: space-around;
  }

  .Weather_location__Wic-L {
    margin: 60px 0;
    padding-top: 0;
  }

  .Weather_weather_wrapper__xFaSS {
    width: 250px;
    margin: 70px 0 0 0;
  }
}


